<script lang="ts">
	import './layout.css';
	import favicon from '$lib/assets/favicon.svg';
	import {
		availableLanguages,
		type Language,
		getTranslation,
		type TranslationKey
	} from '$lib/i18n';
	import { goto } from '$app/navigation';
	import { cart } from '$lib/stores/cart.svelte';

	let { children, data } = $props();

	let language = $derived<Language>(data.language);

	function t(key: TranslationKey) {
		return getTranslation(language, key);
	}

	async function changeLanguage(lang: Language) {
		// Set cookie and reload
		await fetch('/api/set-language', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({ language: lang })
		});
		goto(window.location.pathname, { invalidateAll: true });
	}
</script>

<svelte:head>
	<link rel="icon" href={favicon} />
</svelte:head>

<!-- Sticky Header -->
{#if data.student}
	<header class="sticky top-0 z-50 bg-white border-b border-gray-200 shadow-sm">
		<div class="max-w-7xl mx-auto px-3 sm:px-6 py-3 sm:py-4">
			<!-- Mobile: Stacked Layout -->
			<div class="flex flex-col gap-3 sm:hidden">
				<!-- Top Row: Student Name -->
				<div>
					<a href="/catalogue" class="block hover:text-blue-600 transition-colors">
						<h1 class="text-base font-semibold text-gray-800">
							{data.student.firstName}
							{data.student.lastName}
						</h1>
					</a>
				</div>
				<!-- Bottom Row: Controls -->
				<div class="flex items-center justify-between gap-2">
					<!-- Language Selector -->
					<div class="flex gap-1">
						{#each availableLanguages as lang (lang)}
							<button
								onclick={() => changeLanguage(lang)}
								class:bg-blue-600={language === lang}
								class:text-white={language === lang}
								class:bg-gray-100={language !== lang}
								class:text-gray-700={language !== lang}
								class="px-2 py-1 rounded text-xs font-medium uppercase hover:bg-blue-500 hover:text-white transition-colors"
							>
								{lang}
							</button>
						{/each}
					</div>
					<div class="flex items-center gap-2">
						<!-- Shopping Cart Button -->
						<button
							onclick={() => cart.toggle()}
							class="relative px-3 py-1.5 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex items-center gap-1"
						>
							<svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
								<path
									stroke-linecap="round"
									stroke-linejoin="round"
									stroke-width="2"
									d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
								/>
							</svg>
							{#if cart.totalItems > 0}
								<span
									class="absolute -top-1 -right-1 bg-red-500 text-white text-xs font-bold rounded-full w-5 h-5 flex items-center justify-center"
								>
									{cart.totalItems}
								</span>
							{/if}
						</button>
						<!-- Logout Button -->
						<form method="POST" action="/logout">
							<button
								class="px-3 py-1.5 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors text-sm"
							>
								{t('logout')}
							</button>
						</form>
					</div>
				</div>
			</div>

			<!-- Desktop: Single Row Layout -->
			<div class="hidden sm:flex justify-between items-center">
				<!-- Left: Student Name -->
				<div>
					<a href="/catalogue" class="block hover:text-blue-600 transition-colors">
						<h1 class="text-lg font-semibold text-gray-800">
							{data.student.firstName}
							{data.student.lastName}
						</h1>
					</a>
				</div>

				<!-- Right: Language Selector & Cart -->
				<div class="flex items-center gap-4">
					<!-- Language Selector -->
					<div class="flex gap-2">
						{#each availableLanguages as lang (lang)}
							<button
								onclick={() => changeLanguage(lang)}
								class:bg-blue-600={language === lang}
								class:text-white={language === lang}
								class:bg-gray-100={language !== lang}
								class:text-gray-700={language !== lang}
								class="px-3 py-1.5 rounded text-sm font-medium uppercase hover:bg-blue-500 hover:text-white transition-colors"
							>
								{lang}
							</button>
						{/each}
					</div>

					<!-- Shopping Cart Button -->
					<button
						onclick={() => cart.toggle()}
						class="relative px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex items-center gap-2"
					>
						<svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
							<path
								stroke-linecap="round"
								stroke-linejoin="round"
								stroke-width="2"
								d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
							/>
						</svg>
						<span class="hidden sm:inline">{t('cart')}</span>
						{#if cart.totalItems > 0}
							<span
								class="absolute -top-2 -right-2 bg-red-500 text-white text-xs font-bold rounded-full w-6 h-6 flex items-center justify-center"
							>
								{cart.totalItems}
							</span>
						{/if}
					</button>

					<!-- Logout Button -->
					<form method="POST" action="/logout">
						<button
							class="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
						>
							{t('logout')}
						</button>
					</form>
				</div>
			</div>
		</div>
	</header>
{/if}

<!-- Shopping Cart Sidebar -->
{#if cart.isOpen}
	<div
		class="fixed inset-0 bg-gray-900/30 backdrop-blur-sm z-50"
		onclick={() => cart.close()}
		onkeydown={(e) => e.key === 'Escape' && cart.close()}
		role="button"
		tabindex="0"
		aria-label="Close cart"
	>
		<div
			class="fixed right-0 top-0 h-full w-full sm:w-96 bg-white shadow-xl overflow-hidden"
			onclick={(e) => e.stopPropagation()}
			onkeydown={(e) => e.stopPropagation()}
			role="dialog"
			aria-label="Shopping cart"
			tabindex="-1"
		>
			<div class="p-4 sm:p-6 h-full flex flex-col">
				<div class="flex justify-between items-center mb-4 sm:mb-6 flex-shrink-0">
					<h2 class="text-xl sm:text-2xl font-bold">{t('shoppingCart')}</h2>
					<button
						onclick={() => cart.close()}
						class="text-gray-500 hover:text-gray-700"
						aria-label="Close cart"
					>
						<svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
							<path
								stroke-linecap="round"
								stroke-linejoin="round"
								stroke-width="2"
								d="M6 18L18 6M6 6l12 12"
							/>
						</svg>
					</button>
				</div>

				{#if cart.isEmpty}
					<div class="flex-1 flex items-center justify-center text-gray-500">
						<div class="text-center">
							<svg
								class="mx-auto h-12 w-12 text-gray-400 mb-4"
								fill="none"
								stroke="currentColor"
								viewBox="0 0 24 24"
							>
								<path
									stroke-linecap="round"
									stroke-linejoin="round"
									stroke-width="2"
									d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
								/>
							</svg>
							<p>{t('cartEmpty')}</p>
						</div>
					</div>
				{:else}
					<div class="flex-1 overflow-y-auto mb-4">
						{#each cart.items as item (item.photoLinkId)}
							<div class="border-b border-gray-200 py-4">
								<div class="flex justify-between items-start mb-2">
									<div class="flex-1">
										<h3 class="font-medium text-gray-800">{item.productName}</h3>
										{#if item.studentName}
											<p class="text-xs text-gray-600 mt-1">{item.studentName}</p>
										{/if}
										{#if item.className || item.schoolName}
											<p class="text-xs text-gray-500">
												{#if item.className}{item.className}{/if}{#if item.className && item.schoolName},
												{/if}{#if item.schoolName}{item.schoolName}{/if}
											</p>
										{/if}
										{#if item.publicationYear}
											<p class="text-xs text-gray-500">{item.publicationYear}</p>
										{/if}
										<p class="text-sm text-gray-600 mt-1">€{item.price.toFixed(2)}</p>
									</div>
									<button
										onclick={() => cart.remove(item.photoLinkId)}
										class="text-red-500 hover:text-red-700"
										aria-label="Remove {item.productName} from cart"
									>
										<svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
											<path
												stroke-linecap="round"
												stroke-linejoin="round"
												stroke-width="2"
												d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
											/>
										</svg>
									</button>
								</div>
								<div class="flex items-center gap-2">
									<button
										onclick={() => cart.updateQuantity(item.photoLinkId, item.quantity - 1)}
										class="px-2 py-1 bg-gray-200 rounded hover:bg-gray-300"
									>
										-
									</button>
									<input
										type="number"
										value={item.quantity}
										onchange={(e) =>
											cart.updateQuantity(item.photoLinkId, parseInt(e.currentTarget.value))}
										class="w-16 text-center border border-gray-300 rounded px-2 py-1"
										min="1"
									/>
									<button
										onclick={() => cart.updateQuantity(item.photoLinkId, item.quantity + 1)}
										class="px-2 py-1 bg-gray-200 rounded hover:bg-gray-300"
									>
										+
									</button>
									<span class="ml-auto font-medium">
										€{(item.price * item.quantity).toFixed(2)}
									</span>
								</div>
							</div>
						{/each}
					</div>

					<div class="border-t border-gray-200 pt-4 flex-shrink-0 bg-white">
						<div class="flex justify-between items-center mb-4">
							<span class="text-base sm:text-lg font-bold">{t('total')}:</span>
							<span class="text-xl sm:text-2xl font-bold text-blue-600"
								>€{cart.total.toFixed(2)}</span
							>
						</div>
						<button
							onclick={() => {
								cart.close();
								goto('/checkout');
							}}
							class="w-full bg-blue-600 text-white py-3 rounded-lg hover:bg-blue-700 transition-colors font-medium"
						>
							{t('checkout')}
						</button>
					</div>
				{/if}
			</div>
		</div>
	</div>
{/if}

{@render children()}
